<template>
    <div class="backToTop" @click="jumpTo">
        <div class="up-arrow">
            <a href="javascript:void(0)">
                <span class="white"></span>
                <span class="gray"></span>
            </a>
        </div>
    </div>
</template>

<script>
export default {
    methods: {
        jumpTo() {
            document.getElementById("top").scrollIntoView()
        }
    }
}
</script>

<style lang="css">
.backToTop{
    position: fixed;
    bottom: 25px;
    right:50px;
    height:50px;
    width:50px;
    background-color: #dfdfdf;
}
.backToTop a{
    position: relative;
    display: block;
    width:50px;
    height:50px;
}
.backToTop .white{
    position: absolute;
    top:-15px;
    border:25px solid;
    border-color: transparent transparent #FFF transparent;
}
.backToTop .gray{
    position: absolute;
    top:-10px;
    border:25px solid;
    border-color:  transparent transparent #dfdfdf transparent;
}
</style>



