import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
export default new Router({
    routes: [
        {
            path: '/',
            redirect: '/home'
        }, {
            path: '/home',
            component: resolve => require(['../views/home.vue'], resolve)
        }, {
            path: '/tag',
            component: resolve => require(['../views/tag.vue'], resolve)
        }, {
            path: '/aboutme',
            component: resolve => require(['../views/aboutme.vue'], resolve)
        }, {
            path: '/article',
            component: resolve => require(['../views/article.vue'], resolve),
            children: [
                {
                    path: 'ajax',
                    component: resolve => require(['../views/articles/ajax.vue'], resolve)
                },
                {
                    path: 'interview',
                    component: resolve => require(['../views/articles/interview.vue'], resolve)
                },
                {
                    path: 'interview2',
                    component: resolve => require(['../views/articles/interview2.vue'], resolve)
                },
                {
                    path: 'new',
                    component: resolve => require(['../views/articles/new.vue'], resolve)
                },
                {
                    path: 'node1',
                    component: resolve => require(['../views/articles/node1.vue'], resolve)
                },
                {
                    path: 'python1',
                    component: resolve => require(['../views/articles/python1.vue'], resolve)
                },
                {
                    path: 'python2',
                    component: resolve => require(['../views/articles/python2.vue'], resolve)
                },
                {
                    path: 'python3',
                    component: resolve => require(['../views/articles/python3.vue'], resolve)
                },
                {
                    path: 'sass',
                    component: resolve => require(['../views/articles/sass.vue'], resolve)
                },
                {
                    path: 'ssr',
                    component: resolve => require(['../views/articles/ssr.vue'], resolve)
                },
                {
                    path: 'vue1',
                    component: resolve => require(['../views/articles/vue1.vue'], resolve)
                },
                {
                    path: 'vue2',
                    component: resolve => require(['../views/articles/vue2.vue'], resolve)
                },
            ]
        }
    ]
})


